@import url(https://fonts.googleapis.com/css2?family=Knewave&family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
:root {
  /* COLORS */

  --white: #fbfbfb;
  --black: #0a0a0a;
  --background: #f1e9da;
  --header: #6e0e0c;
  --add: #407076;
  --add-lighter: #4e888f;
  --list: #f28482;
  --list-lighter: #f49c9b;
  --search: #bba0ca;
  --search-lighter: #c8b3d4;
  --search-light: #f1ecf4;
  --help: #e1ca96;
  --help-lighter: #ead9b5;
  --shadow-dark: rgba(0, 0, 0, 0.2);
  --shadow-light: rgba(255, 255, 255, 0.2);

  /* TRANSITIONS */

  --btn-trans: transform 400ms 50ms;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Oswald", sans-serif;
  color: #0a0a0a;
  color: var(--black);
  background-color: #f1e9da;
  background-color: var(--background);
  /* min-height: 100vh;
  display: grid;
  align-items: center; */
}

section {
  margin: 0.5em auto;
  padding: 3em 0;
  text-align: center;
}

p {
  font-weight: 300;
}

.form-control {
  border: none;
}

input,
select,
textarea {
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.2), -3px -3px 7px rgba(255, 255, 255, 0.2);
  box-shadow: 3px 3px 7px var(--shadow-dark), -3px -3px 7px var(--shadow-light);
}

.section-main {
  width: 90%;
  max-width: 700px;
}

.my-card-deck {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-white {
  border-radius: 0.25rem;
  background-color: #fbfbfb;
  background-color: var(--white);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2),
    -5px -5px 15px rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 15px var(--shadow-dark),
    -5px -5px 15px var(--shadow-light);
}

article {
  width: 90%;
  max-width: 400px;
  margin: 2em;
  padding: 1em 2em 2em;
}

.btn {
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.2), -3px -3px 7px rgba(255, 255, 255, 0.2);
  box-shadow: 3px 3px 7px var(--shadow-dark), -3px -3px 7px var(--shadow-light);
  text-transform: uppercase;
  transition: transform 400ms 50ms;
  transition: var(--btn-trans);
}

.btn:hover {
  transform: scale(1.1);
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  margin-top: 2em;
}

h1 {
  font-family: "Knewave", cursive;
  color: var(--header);
  font-size: 5.5rem;
  margin-bottom: 5vh;
}

/* h2 {
  font-weight: 600;
  font-size: 1.75rem;
  margin: 0;
} */

@media screen and (min-width: 1024px) {
  header {
    flex-direction: row;
    margin-top: 5em;
  }

  h1 {
    font-size: 8rem;
    margin-right: 0.3em;
  }
}

@media screen and (min-width: 1400px) {
  h1 {
    font-size: 10rem;
  }
}

.links {
  width: 200px;
  height: 200px;
  position: relative;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  place-items: center;

  margin-bottom: 5vh;
}

.link {
  border: none;
  background-color: transparent;
  width: 115%;
}

.link svg {
  transition: var(--btn-trans);
}

.link svg:hover {
  transform: scale(1.2);
}

.icon-add {
  grid-area: 2/1/3/2;
}

.icon-add:hover circle {
  fill: var(--add-lighter);
}

.icon-list {
  grid-area: 2/3/3/4;
}

.icon-list:hover circle {
  fill: var(--list-lighter);
}

.icon-search {
  grid-area: 3/2/4/3;
  position: relative;
  top: -8%;
}

.icon-search:hover circle {
  fill: var(--search-lighter);
}

.icon-help {
  grid-area: 1/2/2/3;
  position: relative;
  top: 9%;
}

.icon-help:hover circle {
  fill: var(--help-lighter);
}

@media screen and (min-width: 900px) {
  .links {
    width: 170px;
    height: 170px;
  }
}

.btn-add {
  background-color: var(--add);
  color: var(--white);
  margin-top: 0.5em;
}

.btn-search {
  background-color: var(--search);
  color: var(--white);
}

.input-wrapper {
  position: relative;
}

.search-list {
  position: absolute;
  width: 100%;
  top: 100%;
  left: 0%;
}

.list-group-item:hover {
  background-color: var(--search-light);
}

.search-results {
  display: grid;
  place-items: center;
}

.buttons-group {
  width: 90%;
  max-width: 500px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 0;
  padding-bottom: 0;
}

.btn-list {
  background-color: var(--list);
  color: var(--white);
  margin-bottom: 0.5em;
}

span {
  font-weight: 600;
}

.sort-active {
  color: var(--black);
}

h3 {
  font-size: 1.3rem;
  text-transform: uppercase;
  font-weight: 500;
}

figure {
  margin-bottom: 2em;
}

img {
  width: 15%;
}

.help {
  padding-bottom: 0;
}

h2 {
  text-transform: uppercase;
  margin-bottom: 1em;
  font-size: 1.5rem;
}

.lead-p {
  font-size: 1.2rem;
}

.info-main {
  order: -1;
}

article svg {
  width: 60px;
  margin-top: 1em;
}

@media screen and (min-width: 1024px) {
  h2 {
    font-size: 1.75rem;
  }
}

@media screen and (min-width: 1200px) {
  .info-main {
    order: 0;
  }
}

@media screen and (min-width: 1400px) {
  h2 {
    font-size: 2rem;
  }
}

